.App {
  text-align: center;
  color: #2d2d2d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  min-width: 300px;
  min-height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

*{margin: 0; padding: 0;}
ul,li{list-style: none;}

aside{
  background: #2d2d2d;
  padding: 50px 20px;
  width: 300px;
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  color: #eeffff;
  position: relative;
}

aside>h1, aside>h2, aside>h3{
  margin: 10px 0;
} 

#navigator{
  margin: 30px 0;
}

#navigator button{
  display: block;
  margin: 20px auto;
  padding: 4px 40px;
  background-color: transparent;
  border: 0.5px solid #eeffff;
  color: inherit;
  cursor: pointer;
  font-size: 1.1em;
  outline-color: rgb(222,255,255);
}

#navigator button:hover{
  -webkit-box-shadow: 0 0 1px 0px #61dafb;
          box-shadow: 0 0 1px 0px #61dafb;
  border: 0.5px solid #61dafb;
  background-color: rgba(100,100,100,0.1);
  color: #61dafb;
}

#navigator button.clicked{
  border: 0.5px solid #61dafb;
  background-color: rgba(100,100,100,0.1);
  color: #61dafb;
}

aside>.more{
  display: -ms-flexbox;
  display: flex;
}

aside>.more>li{
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
}

aside>.more>li:hover>a{
  color: #61dafb;
}

aside>.more>li>a{
  text-decoration: none;
  color: inherit;
  font-size: 2em;
}

aside>.more>li>p{
  visibility: hidden;
  position: absolute;
  top: -145%;
  left: 0;
  border:1px solid #61dafb;
  color: #61dafb;
  border-radius: 3px;
  padding: 2px 5px 5px;
  background-color: #2d2d2d;
  -webkit-transform: translateY(20%);
      -ms-transform: translateY(20%);
          transform: translateY(20%); 
}

aside>.more>li>p::before{
  content: '';
  width: 8px;
  height: 8px;
  display: block;
  position: absolute;
  border: inherit;
  border-top: none;
  border-left: none;
  border-radius: none;
  top: 92%;
  left: 40%;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  background-color: #2d2d2d;
}

aside>.more>li:hover>p{
  visibility: visible; 
  -webkit-transform: translateY(0); 
      -ms-transform: translateY(0); 
          transform: translateY(0);
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.App>aside>.menu{
  font-size: 2em;
  padding: 5px 8px;
  position: absolute;
  left: 100%;
  top: 0;
  display: none;
}

/****当屏幕过窄时隐藏左侧边栏****/
@media(max-width: 700px){
  .App>aside{
    position: fixed;
    right: 100%;
    padding: 0px;
    min-height: 100vh;
    z-index: 1;
  }

  .App>aside>.menu{
    display: block;
  }

  .App>.main{
    min-height: 100vh;
  }
}

.App>aside.showMenu{
  right: auto;
}

.main{
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.highlight-pink-background{
    background: #cc7a6f;
    color: #fafafa;
}

.highlight-pink{
  color: #cc7a6f;
}

